@import './../../styles/index.scss';

.container {
  display: flex;
  justify-content: center;
}

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  padding: 20px;
}

.tab {
  list-style-type: none;
  background-color: $c-btn-bg;
  padding: 10px 15px;
  border-radius: 20px;
  border: 2px solid $c-header;
  color: $c-header;
  transition: $t-normal;
  cursor: pointer;

  &:hover {
    background: $c-btn-hover;
    color: $c-white;
  }
}
