@import './../../styles/index.scss';

.button {
  background-color: $c-btn-bg;
  padding: 6px 10px;
  width: fit-content;
  border: 2px solid $c-btn-hover;
  border-radius: 5px;
  color: $c-btn-hover;
  transition: $t-normal;
  cursor: pointer;

  &:hover {
    background-color: $c-heading;
  }
}
